:root {
  font-size: 10px;
}

body {
  font-size: 1.8rem;
  font-weight: 400;
  font-family: "Roboto";
}

.title {
  font-size: 6rem;
}

.bg-separator {
  width: 100%;
  height: 100px;
}

.bg-separator.light-to-grey {
  background-image: linear-gradient(0deg, #f6f7fa 0%, #ffffff 100%);
}

.bg-separator.grey-to-light {
  background-image: linear-gradient(0deg, #ffffff 0%, #f6f7fa 100%);
}

.btn-circle {
  overflow: hidden;
  position: relative;
}

.circle {
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.circle--growing {
  animation: growingCircle 1s ease-out forwards;
}

@keyframes growingCircle {
  0% {
    opacity: 0;
    transform: scale(1);
  }
  10% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: scale(20);
  }
}

.header {
  position: absolute;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  padding-top: 50px;
}

.header__inner {
  margin: auto;
  max-width: 1670px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__menu-item {
  color: #ffffff;
  margin-right: 45px;
  transition: .2s;
}

.header__menu-item:hover {
  color: #8492af;
}

.header__btn {
  color: #5584ff;
  padding: 15px 43px;
  border: 1px solid rgba(85, 132, 255, 0.3);
  border-radius: 24px;
}

.burger-btn {
  position: relative;
  display: none;
  width: 60px;
  height: 60px;
  border: 2px solid #8492af;
  border-radius: 10px;
}

.burger-btn > span {
  height: 2px;
  transition-property: height;
  transition-delay: .3s;
  width: 40px;
  position: absolute;
  left: 8px;
  top: 50%;
  background-color: #8492af;
}

.burger-btn > span::before, .burger-btn > span::after {
  content: '';
  position: absolute;
  width: 40px;
  height: 2px;
  background-color: #8492af;
}

.burger-btn--disabled > span::before {
  animation: defaultBurgerBefore .6s forwards;
}

.burger-btn--disabled > span::after {
  animation: defaultBurgerAfter .6s forwards;
}

.burger-btn--active > span {
  height: 0;
}

.burger-btn--active > span::before {
  animation: rotateBurgerBefore .6s forwards;
}

.burger-btn--active > span::after {
  animation: rotateBurgerAfter .6s forwards;
}

@keyframes rotateBurgerBefore {
  0% {
    transform: translateY(-10px) rotate(0);
  }
  50% {
    transform: translateY(0px) rotate(0);
  }
  100% {
    transform: translateY(0px) rotate(45deg);
  }
}

@keyframes rotateBurgerAfter {
  0% {
    transform: translateY(10px) rotate(0);
  }
  50% {
    transform: translateY(0px) rotate(0);
  }
  100% {
    transform: translateY(0px) rotate(-45deg);
  }
}

@keyframes defaultBurgerBefore {
  0% {
    transform: translateY(0px) rotate(45deg);
  }
  50% {
    transform: translateY(0px) rotate(0);
  }
  100% {
    transform: translateY(-10px) rotate(0);
  }
}

@keyframes defaultBurgerAfter {
  0% {
    transform: translateY(0px) rotate(-45deg);
  }
  50% {
    transform: translateY(0px) rotate(0);
  }
  100% {
    transform: translateY(10px) rotate(0);
  }
}

