.dragndrop {
  background-color: #0e1a35;
  background-image: url(../img/bg/grid.png), url(../img/bg/graph2.png);
  background-position: center, center;
  background-repeat: no-repeat, no-repeat;
  background-size: contain, contain;
  padding-top: 160px;
  padding-bottom: 180px;
}

.dragndrop__inner {
  max-width: 1710px;
  padding-left: 15px;
  padding-right: 15px;
  width: 100%;
  margin: auto;
  display: flex;
  justify-content: space-between;
}

.dragndrop__demo {
  flex-basis: 50%;
}

.dragndrop__content {
  flex-basis: 50%;
  padding-left: 180px;
  padding-right: 190px;
}

.dragndrop__title {
  font-weight: 100;
  color: #ffffff;
  margin-bottom: 45px;
}

.dragndrop__text {
  line-height: 30px;
  color: #8492af;
  margin-bottom: 50px;
}

.dragndrop__text > p {
  margin-bottom: 40px;
}

.dragndrop__nav {
  display: flex;
}

.dragndrop__arrow-left, .dragndrop__arrow-right {
  width: 35px;
  height: 35px;
  border: 1px solid #a5aec3;
  border-radius: 50%;
  margin-right: 20px;
  position: relative;
}

.dragndrop__arrow-left::after, .dragndrop__arrow-right::after {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
}

.dragndrop__arrow-left::after {
  border-right: 10px solid #a5aec3;
  transform: translate(-60%, -50%);
}

.dragndrop__arrow-right::after {
  border-left: 10px solid #a5aec3;
  transform: translate(-40%, -50%);
}

.dragndrop-demo {
  display: flex;
  justify-content: space-between;
}

.dragndrop-demo__column {
  flex-basis: 45%;
}

.dragndrop-demo__title {
  font-size: 3rem;
  font-weight: 500;
  color: #ffffff;
  position: relative;
  margin-bottom: 28px;
}

.dragndrop-demo__title > span {
  color: #8492af;
}

.dragndrop-demo__title--arrow::before, .dragndrop-demo__title--arrow::after {
  content: '';
  display: block;
  height: 3px;
  border-radius: 1px;
  width: 15px;
  background-color: #8492af;
  position: absolute;
  right: 0;
  top: 50%;
}

.dragndrop-demo__title--arrow::before {
  transform: rotate(45deg);
  transform-origin: right bottom;
}

.dragndrop-demo__title--arrow::after {
  transform: rotate(-45deg);
  transform-origin: right top;
}

.dragndrop-demo__item {
  margin-bottom: 10px;
}

.dragndrop-demo__item > img {
  width: 100%;
}

.dragndrop-demo__item--dragging {
  position: relative;
  left: 25px;
  top: 35px;
}

.dragndrop-demo__item--dragging::after {
  content: url(../img/content/dragndrop/hand.png);
  width: 76px;
  height: 76px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: rgba(132, 146, 175, 0.2);
  position: absolute;
  bottom: -42px;
  right: -26px;
}

.dragndrop-demo__item--dragging::before {
  content: '';
  display: block;
  width: 96px;
  height: 96px;
  border-radius: 50%;
  background-color: rgba(132, 146, 175, 0.2);
  position: absolute;
  bottom: -52px;
  right: -36px;
}

.dragndrop-demo__statement {
  font-size: 2rem;
  font-weight: 500;
  color: #8492af;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
}

.description {
  background-color: #f6f7fa;
  padding-top: 100px;
}

.description__title {
  margin: auto;
  font-weight: 100;
  color: #0e1a35;
  margin-bottom: 50px;
  text-align: center;
}

.description__subtitle {
  margin: auto;
  line-height: 30px;
  color: #8492af;
  margin-bottom: 80px;
  text-align: center;
  max-width: 1000px;
}

.description__content {
  margin: auto;
  max-width: 1400px;
  display: flex;
  justify-content: center;
  position: relative;
}

.description-info {
  max-width: 250px;
  position: absolute;
}

.description-info__title {
  font-size: 2.4rem;
  font-weight: 700;
  color: #0e1a35;
  margin-bottom: 15px;
}

.description-info__line-block {
  position: relative;
  margin-bottom: 30px;
}

.description-info__line {
  height: 2px;
  background-color: #5584ff;
  position: absolute;
  top: 0px;
  width: 635px;
}

.description-info__text {
  line-height: 30px;
  color: #8492af;
}

.description-info--left-top {
  left: 0;
  top: 90px;
}

.description-info--left-top .description-info__line {
  left: 0;
}

.description-info--left-top .description-info__line::before, .description-info--left-top .description-info__line::after {
  content: '';
  display: block;
  position: absolute;
  left: 100%;
  top: 50%;
  border-radius: 50%;
}

.description-info--left-top .description-info__line::after {
  transform: translateY(-50%);
  width: 30px;
  height: 30px;
  background-color: #5584ff;
}

.description-info--left-top .description-info__line::before {
  transform: translate(-8px, -50%);
  width: 46px;
  height: 46px;
  background-color: rgba(85, 133, 255, 0.6);
}

.description-info--right-bottom {
  right: 0;
  bottom: 155px;
}

.description-info--right-bottom .description-info__line {
  right: 0;
}

.description-info--right-bottom .description-info__line::before, .description-info--right-bottom .description-info__line::after {
  content: '';
  display: block;
  position: absolute;
  right: 100%;
  top: 50%;
  border-radius: 50%;
}

.description-info--right-bottom .description-info__line::after {
  transform: translateY(-50%);
  width: 30px;
  height: 30px;
  background-color: #5584ff;
}

.description-info--right-bottom .description-info__line::before {
  transform: translate(8px, -50%);
  width: 46px;
  height: 46px;
  background-color: rgba(85, 133, 255, 0.6);
}

