@charset "utf-8";

@import "basic/fonts";
@import "basic/basic";
@import "blocks/header";
@import "blocks/footer";


.first-screen {
  background-color: $main-color;
  background-image: url(../img/bg/grid.png), url(../img/bg/graph2.png);
  background-position: center, center;
  background-repeat: no-repeat, no-repeat;
  background-size: contain, contain;
  padding-top: 20vh;
  &__inner {}
  &__title {
    font-weight: 100;
    color: #ffffff;
    text-align: center;
    margin-bottom: 15px;
  }
  &__subtitle {
    font-size: 3.6rem;
    color: #8492af;
    text-align: center;
    margin-bottom: 150px;
  }
  &__img {
    background-color: #274790;
    max-width: 1024px;
    width: 100%;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    & > img {
      position: relative;
      top: 70px;
    }
  }
}

.features {
  padding-top: 250px;
  padding-bottom: 120px;
  &__row {
    display: flex;
    justify-content: space-between;
    max-width: 1350px;
    margin: auto;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-basis: 25%;
    padding-left: 25px;
    padding-right: 25px;
    min-width: 280px;
  }
  &__img {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
    min-height: 100px;
  }
  &__title {
    font-size: 2.4rem;
    font-weight: 700;
    color: $main-color;
    margin-bottom: 28px;
    text-align: center;
  }
  &__text {
    line-height: 30px;
    color: #8492af;
    text-align: center;
    margin-bottom: 50px;
  }
  &__btn {
    font-weight: 700;
    color: $main-color;
    padding: 15px 85px;
    border: 1px solid rgba(132,146,175,0.3);
    border-radius: 24px;
    transition: .2s;
    white-space: nowrap;
  }
}

.features2 {
  padding-top: 100px;
  padding-bottom: 100px;
  background: no-repeat 110% center / 50%;
  background-color: #f6f7fa;
  background-image: url(../img/bg/app.png);
  &__inner {
    max-width: 1470px;
    width: 100%;
    margin: auto;
  }
  &__content {
    max-width: 52%;
  }
  &__title {
    font-weight: 100;
    color: $main-color;
    margin-bottom: 60px;
  }
  &__text {
    line-height: 30px;
    color: #8492af;
    margin-bottom: 65px;
    & > p {
      margin-bottom: 40px;
    }
  }
  &__list {}
  &__list-item {
    font-size: 2.2rem;
    color: #5584ff;
    margin-bottom: 35px;
    &::before {
      content: url(../img/icon/checked.png);
      vertical-align: middle;
      margin-right: 18px;
    }
  }
}

.dragndrop {
  background-color: $main-color;
  background-image: url(../img/bg/grid.png), url(../img/bg/graph2.png);
  background-position: center, center;
  background-repeat: no-repeat, no-repeat;
  background-size: contain, contain;
  padding-top: 160px;
  padding-bottom: 180px;
  &__inner {
    max-width: 1710px;
    padding-left: 15px;
    padding-right: 15px;
    width: 100%;
    margin: auto;
    display: flex;
    justify-content: space-between;
  }
  &__demo {
    flex-basis: 50%;
  }
  &__content {
    flex-basis: 50%;
    padding-left: 180px;
    padding-right: 190px;
  }
  &__title {
    font-weight: 100;
    color: #ffffff;
    margin-bottom: 45px;
  }
  &__text {
    line-height: 30px;
    color: #8492af;
    margin-bottom: 50px;
    & > p {
      margin-bottom: 40px;
    }
  }
  &__nav {
    display: flex;
  }
  &__arrow-left,
  &__arrow-right {
    $size: 35px;
    width: $size;
    height: $size;
    border: 1px solid #a5aec3;
    border-radius: 50%;
    margin-right: 20px;
    position: relative;
  }
  $sizeSmall: 8px;
  $sizeBig: 10px;
  &__arrow-left::after,
  &__arrow-right::after {
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 0;
    height: 0;
    border-top: $sizeSmall solid transparent;
    border-bottom: $sizeSmall solid transparent;
  }
  &__arrow-left::after {
    border-right: $sizeBig solid #a5aec3;
    transform: translate(-60%, -50%);
  }
  &__arrow-right::after {
    border-left: $sizeBig solid #a5aec3;
    transform: translate(-40%, -50%);
  }
}
.dragndrop-demo {
  display: flex;
  justify-content: space-between;

  &__column {
    flex-basis: 45%;
  }
  &__title {
    font-size: 3rem;
    font-weight: 500;
    color: #ffffff;
    position: relative;
    margin-bottom: 28px;
    & > span {
      color: #8492af;
    }
    &--arrow::before,
    &--arrow::after {
      content: '';
      display: block;
      height: 3px;
      border-radius: 1px;
      width: 15px;
      background-color: #8492af;
      position: absolute;
      right: 0;
      top: 50%;
    }
    &--arrow::before {
      transform: rotate(45deg);
      transform-origin: right bottom;
    }
    &--arrow::after {
      transform: rotate(-45deg);
      transform-origin: right top;
    }
  }
  &__item {
    margin-bottom: 10px;
    & > img {
      width: 100%;
    }
    &--dragging {
      position: relative;
      left: 25px;
      top: 35px;
      &::after {
        content: url(../img/content/dragndrop/hand.png);
        width: 76px;
        height: 76px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        background-color: rgba(132, 146, 175, 0.2);
        position: absolute;
        bottom: -42px;
        right: -26px;
      }
      &::before {
        content: '';
        display: block;
        width: 96px;
        height: 96px;
        border-radius: 50%;
        background-color: rgba(132, 146, 175, 0.2);
        position: absolute;
        bottom: -52px;
        right: -36px;
      }
    }
  }
  &__statement {
    font-size: 2rem;
    font-weight: 500;
    color: #8492af;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
  }
}

.description {
  background-color: #f6f7fa;
  padding-top: 100px;
  &__title {
    margin: auto;
    font-weight: 100;
    color: $main-color;
    margin-bottom: 50px;
    text-align: center;
  }
  &__subtitle {
    margin: auto;
    line-height: 30px;
    color: #8492af;
    margin-bottom: 80px;
    text-align: center;
    max-width: 1000px;
  }
  &__content {
    margin: auto;
    max-width: 1400px;
    display: flex;
    justify-content: center;
    position: relative;
  }
  &__img {}
}

.description-info {
  max-width: 250px;
  position: absolute;
  &__title {
    font-size: 2.4rem;
    font-weight: 700;
    color: $main-color;
    margin-bottom: 15px;
  }
  &__line-block {
    position: relative;
    margin-bottom: 30px;
  }
  &__line {
    height: 2px;
    background-color: #5584ff;
    position: absolute;
    top: 0px;
    width: 635px;
  }
  &__text {
    line-height: 30px;
    color: #8492af;
  }
}
.description-info--left-top {
  left: 0;
  top: 90px;
  & .description-info__line {
    left: 0;
    &::before,
    &::after {
      content: '';
      display: block;
      position: absolute;
      left: 100%;
      top: 50%;
      border-radius: 50%;
    }
    &::after {
      transform: translateY(-50%);
      width: 30px;
      height: 30px;
      background-color: #5584ff;
    }
    &::before {
      transform: translate(-8px,-50%);
      width: 46px;
      height: 46px;
      background-color: rgba(85, 133, 255, 0.6);
    }
  }
}
.description-info--right-bottom {
  right: 0;
  bottom: 155px;
  & .description-info__line {
    right: 0;
    &::before,
    &::after {
      content: '';
      display: block;
      position: absolute;
      right: 100%;
      top: 50%;
      border-radius: 50%;
    }
    &::after {
      transform: translateY(-50%);
      width: 30px;
      height: 30px;
      background-color: #5584ff;
    }
    &::before {
      transform: translate(8px,-50%);
      width: 46px;
      height: 46px;
      background-color: rgba(85, 133, 255, 0.6);
    }
  }
}

.pricing {
  padding-bottom: 100px;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title {
    margin: auto;
    text-align: center;
    font-weight: 100;
    color: $main-color;
    margin-bottom: 90px;
  }
  &__row {
    max-width: 1360px;
    width: 100%;
    margin-bottom: 110px;
    display: flex;
    justify-content: space-between;
  }
  &__plan {
    flex-shrink: 0;
    flex-grow: 0;
  }
  &__text {
    max-width: 680px;
    margin: auto;
    text-align: center;
    line-height: 30px;
    color: #8492af;
    margin-bottom: 65px;
  }
  &__btn {
    padding: 16px 60px;
    font-weight: 700;
    color: #ffffff;
    background-color: #5584ff;
    border-radius: 24px;
    margin: auto;
  }
}

.pricing-plan {
  padding: 50px 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__title {
    font-size: 2.4rem;
    color: $main-color;
    margin-bottom: 55px;
    text-transform: uppercase;
  }
  &__price {
    font-size: 4rem;
    font-weight: 700;
    color: $main-color;
    margin-bottom: 50px;
    &::after {
      content: '/mon';
      font-size: 2.4rem;
      color: #8492af;
    }
  }
  &__price-num {
    font-size: 7.2rem;
  }
  &__features-list {}
  &__feature {
    color: #8492af;
    margin-bottom: 20px;
    &::before {
      vertical-align: middle;
      margin-right: 15px;
    }
    &--include::before {
      content: url(../img/icon/checked.png);
    }
    &--not-include::before {
      content: url(../img/icon/unchecked.png);
    }
  }
}