.header {
  position: absolute;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  padding-top: 50px;
  &__inner {
    margin: auto;
    max-width: 1670px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {}
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__menu-item {
    color: #ffffff;
    margin-right: 45px;
    transition: .2s;
    &:hover {
      color: #8492af;
    }
  }
  &__btn {
    color: #5584ff;
    padding: 15px 43px;
    border: 1px solid rgba(85,132,255,0.3);
    border-radius: 24px;
  }
}

.burger-btn {
  position: relative;
  display: none;
  width: 60px;
  height: 60px;
  border: 2px solid #8492af;
  border-radius: 10px;
  & > span {
    height: 2px;
    transition-property: height;
    transition-delay: .3s;
    width: 40px;
    position: absolute;
    left: 8px;
    top: 50%;
    background-color: #8492af;
    &::before,
    &::after {
      content: '';
      position: absolute;
      width: 40px;
      height: 2px;
      background-color: #8492af;
    }
  }
  &--disabled {
    & > span {
      &::before {
        animation: defaultBurgerBefore .6s forwards;
      }
      &::after {
        animation: defaultBurgerAfter .6s forwards;
      }
    }
  }
  &--active {
    & > span {
      height: 0;
      &::before {
        animation: rotateBurgerBefore .6s forwards;
      }
      &::after {
        animation: rotateBurgerAfter .6s forwards;
      }
    }
  }
}
@keyframes rotateBurgerBefore {
  0% {transform: translateY(-10px) rotate(0);}
  50% {transform: translateY(0px) rotate(0);}
  100% {transform: translateY(0px) rotate(45deg);}
}
@keyframes rotateBurgerAfter {
  0% {transform: translateY(10px) rotate(0);}
  50% {transform: translateY(0px) rotate(0);}
  100% {transform: translateY(0px) rotate(-45deg);}
}
@keyframes defaultBurgerBefore {
  0% {transform: translateY(0px) rotate(45deg);}
  50% {transform: translateY(0px) rotate(0);}
  100% {transform: translateY(-10px) rotate(0);}
}
@keyframes defaultBurgerAfter {
  0% {transform: translateY(0px) rotate(-45deg);}
  50% {transform: translateY(0px) rotate(0);}
  100% {transform: translateY(10px) rotate(0);}
}