.footer {
  background-color: $main-color;
  padding-top: 60px;
  padding-bottom: 60px;
  &__inner {
    margin: auto;
    max-width: 1450px;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  &__copyright {
    color: #8492af;
  }
  &__links {
    display: flex;
  }
  &__link {
    margin-right: 50px;
    color: #8492af;
  }
  &__social {
    display: flex;
  }
  &__social-link {
    margin-right: 20px;
  }
}