$main-color: #0e1a35;
$roboto: 'Roboto';
$mont: 'Montserrat';
:root {
  font-size: 10px;
}
body {
  font-size: 1.8rem;
  font-weight: 400;
  font-family: $roboto;
}
.title {
  font-size: 6rem;
}
.bg-separator {
  width: 100%;
  height: 100px;
  &.light-to-grey {
    background-image: linear-gradient(0deg, #f6f7fa 0%, #ffffff 100%);
  }
  &.grey-to-light {
    background-image: linear-gradient(0deg, #ffffff 0%, #f6f7fa 100%);
  }
}


.btn-circle {
  overflow: hidden;
  position: relative;
}

.circle {
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  &--growing {
    animation: growingCircle 1s ease-out forwards;
  }
}

@keyframes growingCircle {
  0% {
    opacity: 0;
    transform: scale(1);
  }
  10% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: scale(20);
  }
}